.button {
    width: 240px;
    height: 80px;

    cursor: pointer;

    background-color: transparent;
    color: var(--branco);
    border: 2px solid var(--branco);

    font-family: RobotoB;
    font-size: 24px;

    transition-duration: 0.4s;
}

.button:hover {
    color: var(--gelo-escuro);
    border-color: var(--gelo-escuro);
}

.button:active {
    color: var(--cinza-claro);
    border-color: var(--cinza-claro);
}