.botao {
    width: 150px;
    height: 50px;
    border-radius: 25px;
    background-color: var(--color-2);
    border: none;

    font-family: RobotoB;
    font-size: 14px;
    color: var(--branco);

    cursor: pointer;
}

.botao:hover {
    background-color: var(--color-3);
}

.botao:active {
    background-color: var(--color-4);
}