.section_top {
    margin: -50px 50px 30px 50px;

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.section_top_inner {
    width: 600px;
    height: 100%;

    gap: 50px;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.section_top_inner:nth-child(2) {
    width: 500px;
    align-self: center;
    justify-content: center;
}

.section_top_inner h2 {
    font-family: RobotoB;
    font-size: 3em;
    color: var(--preto);

    filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.25));
}

.section_top_inner p {
    font-family: Roboto;
    font-size: 16px;
    color: var(--preto);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.section_top_inner img {
    height: 100%;
    width: 100%;

    transition-duration: 0.4s;
    filter: drop-shadow(6px 6px 6px rgba(0, 0, 0, 0.25));
}

.linguagens {
    height: 150px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

}

.section_middle {
    width: 100%;
    height: 300px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background-image: url(../../assets/img/middle-bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.section_middle div:nth-child(1) {
    width: 100%;
    height: 50px;

    background: linear-gradient(to bottom, var(--gelo-top), transparent);
}

.section_middle div:nth-child(2) {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 160px;
}

.section_middle div:nth-last-child(1) {
    width: 100%;
    height: 50px;

    background: linear-gradient(to top, var(--gelo-top), transparent);
}

.section_bottom_title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    margin: 40px 0px;
}

.section_bottom_title h2 {
    font-family: RobotoB;
    font-size: 32px;
    filter: drop-shadow(6px 6px 6px rgba(0, 0, 0, 0.25));
}

.section_bottom {
    height: 520px;

    margin: 0px 10%;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

