.container {
    display: grid;
    grid-template-rows: 350px 350px 350px 350px 350px;
    grid-template-columns: auto;
    column-gap: 15px;
    row-gap: 15px;
    grid-template-areas: 
    "A B C C"
    "A D D E"
    "F F G H"
    "I J G K"
    "I L L K";
    margin: 15px 15px;
}

.h2 {
    font-family: RobotoB;
    font-size: 3em;
    text-align: center;
    color: var(--preto);

    margin: 50px 0px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}