.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    border-radius: 15px;
    padding: 25px 0px;
}

.container h2  {
    font-family: RobotoB;
    font-size: 32px;
    color: var(--branco);
}

.container p {
    font-family: Roboto;
    color: var(--branco);
}