.section_top {
    margin: -50px 50px 30px 50px;

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.section_top_inner {
    width: 600px;
    height: 100%;

    gap: 50px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.section_top_inner:nth-child(2) {
    width: 500px;
    align-self: center;
    justify-content: center;
}

.section_top_inner h2 {
    font-family: RobotoB;
    font-size: 2.5em;
    color: var(--preto);

    filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.25));
}

.section_top_inner p {
    font-family: Roboto;
    font-size: 16px;
    color: var(--preto);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.section_top_inner img {
    margin-top: -100px;
    height: 500px;

    border-radius: 80px;

    transition-duration: 0.4s;
    filter: drop-shadow(3px 3px 3px var(--color-3));
}