.Fundo {
    margin-top: -20px;
    width: 100%;
    user-select: none;
}

.MenuSuperior {
    width: 100%;
    height: 134px;
    padding: 0 64px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: 0;
    position: absolute;
}

.Logo {
    font-family: RobotoB;
    font-size: 26px;
    color: var(--color-2);
}

.Logo2 {
    color: var(--color-3);
}

.inline {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 50px;
}

.ul {
    list-style-type: none;
    user-select: none;

    display: flex;
    flex-direction: row;
    gap: 50px;
}

.pages {
    text-decoration: none;
    font-family: RobotoB;
    font-size: 18px;
    color: var(--cinza-claro);
}

.pages:hover {
    cursor: pointer;
    color: var(--color-2);
}