.container {
    width: 280px;
    height: 400px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    border-radius: 15px;
    box-shadow: inset 7px 7px 14px #bebebe,
    inset -7px -7px 14px #ffffff;
    
}

.imagem {
    width: 150px;
    height: 150px;

    margin: 20px 0px;

    border-radius: 50%;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.p {
    font-family: Roboto;
    text-align: center;
    margin: 0px 10px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.h4 {
    cursor: pointer;
    font-family: RobotoB;
    margin-bottom: 15px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    transition-duration: 0.2s;
}

.h4:hover {
    color: var(--color-3);
    transform: scale(1.1);
}