:root {
    --color-1: #BAE9FD;
    --color-2: #5D90DC;
    --color-3: #1D68D8;
    --color-4: #0B408F;

    --gelo-top: #EDEDED;
    --gelo-claro: #e0e0e0;
    --gelo-escuro: #bebebe;
    --cinza-claro: #888888;
    --cinza-escuro: #2c2c2c;
    --preto: #121212;
    --branco: #fff;
}

@font-face {
    font-family: Roboto;
    src: url(../assets/fonts/Roboto-Regular.ttf);
}

@font-face {
    font-family: RobotoB;
    src: url(../assets/fonts/Roboto-Bold.ttf);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body, html {
    min-height: 100vh;
    background-color: var(--gelo-top);
}

#root {
    height: 100%;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: var(--cinza-claro); 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: var(--gelo-escuro); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: var(--preto); 
  }

.color2 {
    color: var(--color-2);
}

.color4 {
    color: var(--color-4);
}
